import React, { Component } from 'react';
import Header from '../components/Header';
import OutputCard from '../components/OutputCard';
import { isMobile } from 'react-device-detect';

class Page4 extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {}

  render() {
    const { profile, output, nextPage } = this.props;
    console.log(output);
    return (
      <div className="page4">
        <div className="header-container">
          <Header name={profile || 'Abi'} />
        </div>
        <div className="page4-padded" />
       {!isMobile && <div className="output-container">
         
           
          <div className="output-card-container">
            <OutputCard ><h1>Recognized Text</h1><br/>{output.nepali}</OutputCard>
          </div>
          <div className="output-card-container">
            <OutputCard ><h1>Output</h1><br/>{output.english}</OutputCard>
          </div>
        </div>}
        {isMobile && <div className="output-mobile-container">
        <div className="output-card-container">
            <OutputCard ><h3>Recognized Text</h3><br/>{output.nepali}</OutputCard>
          </div>
          <div className="output-card-container">
            <OutputCard ><h3>Output</h3><br/>{output.english}</OutputCard>
          </div>
        </div>}
       
        <div className="page4-padded" />

        <div className="retake-button-container">
          <div className="retake-button" onClick={() => nextPage()}>
            Retake
          </div>
        </div>
      </div>
    );
  }
}
export default Page4;



